export default {
	data() {
		return {
			videoList: [
				{
					img: '../../../static/image/cardimg.jpeg',
					title: '计算机职业技能课程',
					time: '40分钟',
					id: 0,
					detail: '本专业是计算机硬件与软件阿萨德考虑过金坷垃十多个快乐就好撒。'
				},
				{
					img: '../../../static/image/cardimg.jpeg',
					title: '计算机平面设计课程',
					time: '1小时40分钟',
					id: 1,
					detail: '本专业是计算机硬件与软件相结合。'
				}
			]
		};
	},
	methods: {
		toVideodetail(e) {
			let index = e.currentTarget.dataset.index;
			uni.navigateTo({
				url: '../../videoClass/videoDetail/videoDetail?id=' + this.videoList[index].id
			});
		}
	}
